import request from 'utils/request'

// 取数据
export function getData (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/listUserShowInfo',
    data
  })
}

export function addData (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/saveUserShow',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

export function getDataById (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/getUserShow',
    data
  })
}

export function updateDataById (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/updateUserShow',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

export function deleteDataById (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/delUserShow',
    data
  })
}



