<template>
  <div class="pictures-container">
    <div class="pictures-empty" v-if="!pictures.length">
      <p>暂无数据</p>
    </div>
    <ul v-else class="pictures-list" v-for="(pictures,i) in picturesArray" :key="i">
      <li v-for="(picture,index) in pictures" :key="index">
        <slot v-bind:picture="picture"></slot>
      </li>
      <template v-if="i===picturesArray.length-1">
        <li v-for="n in supply" class="supply-li" :key="'supply'+n">
          <slot v-bind:picture="{}"></slot>
        </li>
      </template>
    </ul>
  </div>
</template>
<script>
export default {
  props: {
    pictures: Array,
    column: {
      type: Number,
      default: 6
    }
  },
  computed: {
    picturesArray: function () {
      let newPictures = JSON.parse(JSON.stringify(this.pictures))
      let array = []
      do {
        array.push(newPictures.splice(0, this.column))
      }
      while (newPictures.length)
      return array
    },
    supply: function () {
      return this.column - this.picturesArray[this.picturesArray.length - 1].length
    }
  }
}
</script>
<style lang='stylus' scoped>
  .pictures-container
    width 100%
    display flex
    flex-direction column
    align-items stretch
  .pictures-list
    display flex
    flex-direction row
    justify-content space-between
    margin-bottom 20px
  .supply-li
    visibility hidden
  .pictures-empty
    flex 1
    display flex
    justify-content center
    align-items center
    height 200px
</style>



