<template>
  <transition name="fade">
    <div class="detail-container" v-show="isReady&&visible" ref="container" @click="handleClose">
      <div class="detail-box" @click.stop>
        <i class="detail-close-btn" @click="handleClose"></i>
        <img class="detail-img" :src="picture.image" alt="">
        <div class="detail-describe">{{picture.signature}}</div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  props: {
    visible: [Boolean, Object]
  },
  watch: {
    'visible': function () {
      if (this.visible) {
        this.record = this.visible
      }
    }
  },
  computed: {
    picture: function () {
      return this.visible ? this.visible : this.record
    }
  },
  data () {
    return {
      isReady: false,
      record: {}
    }
  },
  mounted () {
    document.getElementsByClassName('layout-body')[0].appendChild(this.$refs.container)
    this.isReady = true
  },
  methods: {
    handleClose () {
      this.$emit('close')
    }
  }
}
</script>
<style lang='stylus' scoped>
  .detail-container
    position absolute
    top 0
    left 0
    z-index 10
    width 100%
    height 100%
    background rgba(0, 0, 0, .2)
    display flex
    align-items center
    justify-content center
  .detail-box
    width 270px
    height 385px
    background white
    display flex
    flex-direction column
    align-items stretch
    position relative
  .detail-close-btn
    position absolute
    top -20px
    right -40px
    width 20px
    height 20px
    background whitesmoke
    cursor pointer
    display inline-block
    background url('~@/assets/close.png') no-repeat center
    background-size 100% 100%
  .detail-img
    flex 1
    overflow hidden
    box-shadow 0 2px 6px 0 rgba(0, 0, 0, 0.2)
  .detail-describe
    line-height 20px
    height 60px
    background white
    overflow hidden
    box-shadow 0 2px 6px 0 rgba(0, 0, 0, 0.1)
    padding 0 20px
    display flex
    align-items center
    position relative
    z-index 2
</style>



